import React from 'react';
import Stat from "./Stat";
import {useSelector} from "react-redux";
import cl from './Stats.module.css'

const Stats = () => {

    const currentCharacter = useSelector(state => state.character)

    return (
        <div className={cl.stats}>
            <h3>   </h3>
            <div className={cl.stats__changeable}>
                <Stat title="" name={"strength"}/>
                <Stat title="" name={"agility"}/>
                <Stat title="" name={"intelligence"}/>
                <Stat title="" name={"charisma"}/>
            </div>
            <div className={cl.stats__computed}>
                <div>  : {3 + +currentCharacter.stats.strength} </div>
                <div> : {10 + +currentCharacter.stats.agility} </div>
                <div> : {+currentCharacter.stats.agility + +currentCharacter.stats.intelligence} </div>
            </div>
        </div>
    );
};

export default Stats;