import React from 'react';
import Skill from "./Skill";
import cl from "./Skills.module.css"

const Skills = () => {
    return (
        <div className={cl.skills}>
            <h3>  : </h3>
            <div className={cl.skills__lists}>
                <ul>
                    :
                    <li>
                        <Skill baseStat={"strength"}  name={"attack"} title=""/>
                    </li>
                </ul>
                <ul>
                    :
                    <li>
                        <Skill baseStat={"agility"} title={""} name={"stealth"} />
                    </li>
                    <li>
                        <Skill baseStat={"agility"} title={"  "} name={"archery"} />
                    </li>
                </ul>
                <ul>
                    :
                    <li>
                        <Skill baseStat={"intelligence"} title={""} name={"learnability"} />
                    </li>
                    <li>
                        <Skill baseStat={"intelligence"} title={""} name={"survival"} />
                    </li>
                    <li>
                        <Skill baseStat={"intelligence"} title={""} name={"medicine"} />
                    </li>
                </ul>
                <ul>
                    :
                    <li>
                        <Skill baseStat={"charisma"} title={""} name={"intimidation"} />
                    </li>
                    <li>
                        <Skill baseStat={"charisma"} title={""} name={"insight"} />
                    </li>
                    <li>
                        <Skill baseStat={"charisma"} title={" "} name={"appearance"} />
                    </li>
                    <li>
                        <Skill baseStat={"charisma"} title={""} name={"manipulation"} />
                    </li>
                </ul>
            </div>
        </div>
    );
};

export default Skills;